
import InvoiceFormView from '@/views/InvoiceFormView.vue'
import InvoiceView from '@/views/InvoiceView.vue'
import { createRouter, createWebHistory } from 'vue-router'

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL || '/'),
  routes: [
    // {
    //   path: '/',
    //   name: 'HomeView',
    //   component: HomeView
    // },
    {
      path: '/',
      name: 'InvoiceFormView',
      component: InvoiceFormView
    },
    {
      path: '/invoice/:guestHouseName/:ownerName/:contactNumber/:guestName/:totalGuests/:totalRooms/:roomNumber?/:checkIn/:checkOut',
      name: 'InvoiceView',
      component: InvoiceView
    }
  ]
})

export default router
